<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="modifyCommunicationEvent('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="modifyCommunicationEvent('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="modifyCommunicationEvent('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('PARTIES')" icon="far fa-users">
          <div class="nv-row mb-base">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('FROM_PARTY') }}</label>
              <party-lookup :title="$t('FROM_PARTY')" :recordValue="communicationEvent.fromPartyId" :recordDisplay="communicationEvent.fromPartyName"  valueField="partyId" displayField="partyName" @select-record="selectFromPartyRecord"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('TO_PARTY') }}</label>
              <party-lookup :title="$t('TO_PARTY')" :recordValue="communicationEvent.toPartyId" :recordDisplay="communicationEvent.toPartyName"  valueField="partyId" displayField="partyName" @select-record="selectToPartyRecord"/>
            </div>
          </div>
        </tab-content>
        <tab-content :title="$t('DETAILS')" icon="far fa-file-alt">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('COMMUNICATION_TYPE') }}</label>
          <vs-select autocomplete class="w-full" v-model="communicationEvent.communicationEventTypeId">
            <vs-select-item v-for="(eventTypeObject, index) in communicationEventTypes" :key="index" :value="eventTypeObject.communicationEventTypeId" :text="eventTypeObject.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('CONTACT_TYPE') }}</label>
          <vs-select autocomplete class="w-full" v-model="communicationEvent.contactMechTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in contactMechTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('STATUS') }}</label>
          <vs-select autocomplete class="w-full" v-model="communicationEvent.statusId">
            <vs-select-item v-for="(statusItem, index) in statusItems" :key="index" :value="statusItem.statusId" :text="statusItem.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('REASON') }}</label>
          <vs-select autocomplete class="w-full" v-model="communicationEvent.reasonEnumId">
            <vs-select-item v-for="(enumeration, index) in reasons" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('START_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="communicationEvent.datetimeStarted" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('END_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="communicationEvent.datetimeEnded" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full">
          <label class="vs-input--label">{{ $t('SUBJECT') }}</label>
          <vs-input class="w-full" type="text" v-model="communicationEvent.subject"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('BODY') }}</label>
          <vs-textarea class="w-full" v-model="communicationEvent.body" height="200"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('NOTES') }}</label>
          <vs-textarea class="w-full" v-model="communicationEvent.note" height="100"/>
        </div>
      </div>
    </tab-content>
    </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import CommunicationEventService from '@/services/nucleus/CommunicationEventService'
import EnumerationService from '@/services/commons/EnumerationService'
import StatusItemService from '@/services/commons/StatusItemService'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'communication-event-modify',
  mixins: [AlertMixin, DatePickerMixin],
  components: {
    FormWizard,
    TabContent,
    PartyLookup,
    Datepicker
  },
  data: function () {
    return {
      communicationEventTypes: [],
      contactMechTypes: [],
      statusItems: [],
      reasons: [],
      communicationEvent: {}
    }
  },
  methods: {
    async load () {
      let self = this
      CommunicationEventService.communicationEvents({
        communicationEventId: this.$route.params.id
      }).then(function (response) {
        self.communicationEvent = response.data.eventList[0] || {}
      })

      CommunicationEventService.communicationEventTypes({
        orderByField: 'description'
      }).then(function (response) {
        self.communicationEventTypes = response.data.eventTypeList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'ContactMechType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.contactMechTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'CommunicationPurpose',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.reasons = response.data.enumerationList || []
      })

      StatusItemService.statusItems({
      }).then(function (response) {
        self.statusItems = response.data.statusItemList || []
      })
    },
    modifyCommunicationEvent (nextAction) {
      let self = this
      CommunicationEventService.modifyCommunicationEvent({
        communicationEventId: this.$route.params.id,
        communicationEventTypeId: this.communicationEvent.communicationEventTypeId,
        contactMechTypeEnumId: this.communicationEvent.contactMechTypeEnumId,
        statusId: this.communicationEvent.statusId,
        fromPartyId: this.communicationEvent.fromPartyId,
        toPartyId: this.communicationEvent.toPartyId,
        startDate: this.communicationEvent.datetimeStarted,
        endDate: this.communicationEvent.datetimeEnded,
        subject: this.communicationEvent.subject,
        body: this.communicationEvent.body,
        note: this.communicationEvent.note,
        reasonEnumId: this.communicationEvent.reasonEnumId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewCommunicationEvent', params: { id: response.data.communicationEventId } })
          } else {
            self.$router.push({ name: 'BrowseCommunicationEvents' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    selectFromPartyRecord (record) {
      this.communicationEvent.fromPartyId = record.partyId || ''
    },
    selectToPartyRecord (record) {
      this.communicationEvent.toPartyId = record.partyId || ''
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
